import { useState } from 'react';
import {
  Card,
  Button,
  Switch,
  Space,
  Typography,
  Alert,
  Spin,
  message,
  Empty,
  Timeline,
  Tag,
  Tooltip,
  Collapse,
  List,
} from 'antd';
import {
  CalendarOutlined,
  ReloadOutlined,
  PlayCircleOutlined,
  ClockCircleOutlined,
  ApartmentOutlined,
} from '@ant-design/icons';
import { useApp } from '../contexts/AppContext';
import { ACTION_TYPES } from '../contexts/AppContext';
import apiService from '../services/apiService';

const { Title, Text, Paragraph } = Typography;
const { Panel } = Collapse;

const DeploymentPlanPage = () => {
  const { dispatch } = useApp();
  const [planData, setPlanData] = useState(null);
  const [includeLevels, setIncludeLevels] = useState(true);
  const [loading, setLoading] = useState(false);

  const generateDeploymentPlan = async () => {
    setLoading(true);
    dispatch({
      type: ACTION_TYPES.SET_LOADING,
      payload: { key: 'plan', value: true },
    });

    try {
      const data = await apiService.generateDeploymentPlan([], includeLevels);
      setPlanData(data);
      
      dispatch({
        type: ACTION_TYPES.SET_DEPLOYMENT_PLAN,
        payload: data,
      });
      
      const stepCount = data.deployment_order ? data.deployment_order.length : 0;
      dispatch({
        type: ACTION_TYPES.SET_STATUS_MESSAGE,
        payload: `Deployment plan generated - ${stepCount} steps`,
      });
      
      message.success(`Generated deployment plan with ${stepCount} steps`);
    } catch (error) {
      message.error(`Failed to generate plan: ${error.message}`);
      console.error('Failed to generate deployment plan:', error);
    } finally {
      setLoading(false);
      dispatch({
        type: ACTION_TYPES.SET_LOADING,
        payload: { key: 'plan', value: false },
      });
    }
  };

  const getResourceIcon = (resourceId) => {
    const id = resourceId?.toLowerCase() || '';
    if (id.includes('vpc')) return 'ðŸŒ';
    if (id.includes('subnet')) return 'ðŸ”—';
    if (id.includes('ec2') || id.includes('instance')) return 'ðŸ’»';
    if (id.includes('rds') || id.includes('database')) return 'ðŸ—„ï¸';
    if (id.includes('security') || id.includes('sg')) return 'ðŸ”’';
    if (id.includes('load') || id.includes('alb') || id.includes('elb')) return 'âš–ï¸';
    if (id.includes('auto') || id.includes('asg')) return 'ðŸ“ˆ';
    return 'ðŸ”§';
  };

  const renderDeploymentOrder = () => {
    if (!planData?.deployment_order || planData.deployment_order.length === 0) {
      return null;
    }

    return (
      <Card 
        title={
          <Space>
            <ClockCircleOutlined />
            <Text strong>Deployment Order</Text>
          </Space>
        }
        style={{ marginBottom: 16 }}
      >
        <Timeline
          mode="left"
          items={planData.deployment_order.map((resourceId, index) => ({
            label: `Step ${index + 1}`,
            children: (
              <Space>
                <span style={{ fontSize: 16 }}>{getResourceIcon(resourceId)}</span>
                <Text strong>{resourceId}</Text>
                <Tag color="blue">Deploy</Tag>
              </Space>
            ),
            color: 'blue',
          }))}
        />
      </Card>
    );
  };

  const renderDeploymentLevels = () => {
    if (!planData?.deployment_levels || planData.deployment_levels.length === 0) {
      return null;
    }

    return (
      <Card
        title={
          <Space>
            <ApartmentOutlined />
            <Text strong>Deployment Levels</Text>
          </Space>
        }
        style={{ marginBottom: 16 }}
      >
        <Paragraph type="secondary" style={{ marginBottom: 16 }}>
          Resources grouped by deployment levels can be deployed in parallel within each level.
        </Paragraph>
        
        <Collapse ghost>
          {planData.deployment_levels.map((level, levelIndex) => (
            <Panel
              header={
                <Space>
                  <Tag color="geekblue">Level {levelIndex + 1}</Tag>
                  <Text strong>{level.length} resources</Text>
                  <Text type="secondary">
                    (Can be deployed in parallel)
                  </Text>
                </Space>
              }
              key={levelIndex}
            >
              <List
                size="small"
                dataSource={level}
                renderItem={(resourceId) => (
                  <List.Item>
                    <Space>
                      <span style={{ fontSize: 14 }}>{getResourceIcon(resourceId)}</span>
                      <Text>{resourceId}</Text>
                    </Space>
                  </List.Item>
                )}
              />
            </Panel>
          ))}
        </Collapse>
      </Card>
    );
  };

  const renderPlanOverview = () => {
    if (!planData) return null;

    const totalResources = planData.deployment_order ? planData.deployment_order.length : 0;
    const totalLevels = planData.deployment_levels ? planData.deployment_levels.length : 0;
    const estimatedTime = planData.estimated_time || 'Unknown';
    const parallelizable = planData.parallelizable_resources || 0;

    return (
      <Card style={{ marginBottom: 16 }}>
        <div style={{ 
          display: 'grid', 
          gridTemplateColumns: 'repeat(auto-fit, minmax(150px, 1fr))', 
          gap: 16, 
          textAlign: 'center' 
        }}>
          <div>
            <Title level={3} style={{ margin: 0, color: '#1890ff' }}>
              {totalResources}
            </Title>
            <Text type="secondary">Total Resources</Text>
          </div>
          
          {totalLevels > 0 && (
            <div>
              <Title level={3} style={{ margin: 0, color: '#52c41a' }}>
                {totalLevels}
              </Title>
              <Text type="secondary">Deployment Levels</Text>
            </div>
          )}
          
          {parallelizable > 0 && (
            <div>
              <Title level={3} style={{ margin: 0, color: '#faad14' }}>
                {parallelizable}
              </Title>
              <Text type="secondary">Parallelizable</Text>
            </div>
          )}
          
          <div>
            <Title level={3} style={{ margin: 0, color: '#722ed1' }}>
              {estimatedTime}
            </Title>
            <Text type="secondary">Est. Time</Text>
          </div>
        </div>
      </Card>
    );
  };

  const renderDependencyInfo = () => {
    if (!planData?.dependency_info) return null;

    return (
      <Card
        title="Dependency Analysis"
        size="small"
        style={{ marginBottom: 16 }}
      >
        <Space direction="vertical" size="small" style={{ width: '100%' }}>
          {planData.dependency_info.critical_path && (
            <div>
              <Text strong>Critical Path:</Text>
              <div style={{ marginTop: 4 }}>
                {planData.dependency_info.critical_path.map((resource, index) => (
                  <Tag key={index} style={{ margin: 2 }}>
                    {resource}
                  </Tag>
                ))}
              </div>
            </div>
          )}
          
          {planData.dependency_info.bottlenecks && planData.dependency_info.bottlenecks.length > 0 && (
            <Alert
              message="Dependency Bottlenecks"
              description={
                <ul style={{ marginBottom: 0 }}>
                  {planData.dependency_info.bottlenecks.map((bottleneck, index) => (
                    <li key={index}>{bottleneck}</li>
                  ))}
                </ul>
              }
              type="warning"
              showIcon
              size="small"
            />
          )}
        </Space>
      </Card>
    );
  };

  const renderPlanActions = () => {
    if (!planData) return null;

    return (
      <Card>
        <Space size="middle">
          <Button
            type="primary"
            icon={<PlayCircleOutlined />}
            size="large"
            onClick={() => {
              message.info('Plan execution integration coming soon');
            }}
          >
            Execute Plan
          </Button>
          
          <Button
            icon={<ReloadOutlined />}
            onClick={generateDeploymentPlan}
            loading={loading}
          >
            Regenerate
          </Button>
          
          <Tooltip title="Export plan as JSON">
            <Button
              onClick={() => {
                const blob = new Blob([JSON.stringify(planData, null, 2)], 
                  { type: 'application/json' });
                apiService.downloadFile(blob, `deployment-plan-${apiService.getCurrentTimestamp()}.json`);
                message.success('Plan exported successfully');
              }}
            >
              Export Plan
            </Button>
          </Tooltip>
        </Space>
      </Card>
    );
  };

  return (
    <div style={{ maxWidth: 1200, margin: '0 auto' }}>
      <div style={{ marginBottom: 24 }}>
        <Title level={2}>Deployment Planning</Title>
        <Paragraph type="secondary">
          Generate optimized deployment strategies with dependency ordering
        </Paragraph>
      </div>

      <Card>
        <Space 
          size="large" 
          style={{ 
            width: '100%', 
            display: 'flex', 
            justifyContent: 'space-between',
            alignItems: 'center',
            flexWrap: 'wrap' 
          }}
        >
          <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
            <Text strong>Include deployment levels:</Text>
            <Tooltip title="Group resources into levels that can be deployed in parallel">
              <Switch
                checked={includeLevels}
                onChange={setIncludeLevels}
                size="small"
              />
            </Tooltip>
          </div>
          
          <Button
            type="primary"
            icon={<CalendarOutlined />}
            onClick={generateDeploymentPlan}
            loading={loading}
            size="large"
          >
            Generate Plan
          </Button>
        </Space>
      </Card>

      {loading ? (
        <div style={{ textAlign: 'center', padding: '50px 0' }}>
          <Spin size="large" />
          <div style={{ marginTop: 16 }}>
            <Text>Generating deployment strategy...</Text>
          </div>
        </div>
      ) : planData ? (
        <>
          {renderPlanOverview()}
          {renderDeploymentOrder()}
          {includeLevels && renderDeploymentLevels()}
          {renderDependencyInfo()}
          {renderPlanActions()}
          
          <Card size="small" style={{ marginTop: 16 }}>
            <Text type="secondary">
              Plan generated at: {new Date().toLocaleString()}
            </Text>
          </Card>
        </>
      ) : (
        <Card style={{ marginTop: 16 }}>
          <Empty
            description="No deployment plan generated yet"
            image={Empty.PRESENTED_IMAGE_SIMPLE}
          >
            <Button type="primary" onClick={generateDeploymentPlan}>
              Generate Deployment Plan
            </Button>
          </Empty>
        </Card>
      )}
    </div>
  );
};

export default DeploymentPlanPage;